<template>
    <div id="chating">
        <div class="right">
            <div class="title" style="border-radius: 10px 0px 0px 0px;">방정보</div>
            <div class="roomInfo">
                <div class="roomName">{{roomInfo.roomName}}</div>
                <div class="roomId">{{roomInfo.roomId}}</div>
            </div>
            <div class="title">유저리스트</div>
            <div class="userList">
                <div class="user" v-for="user in userList" :key="user" :class="{my:user.id === socket.id}">{{user.nickName}}</div>
            </div>
        </div>

        <div class="left">
            <div class="publicChat">
                <div class="title" style="border-radius: 0px 10px 0px 0px;">채팅</div>
                <div class="chating">
                <div class="chat" v-for="chat in chatList" :key="chat" :class="{my:chat.id === socket.id}">{{chat.nickName}} : {{chat.msg}}<i class="fa-solid fa-m"></i></div>
              </div>
              <div class="send">
                <input type="text" class="form-control" placeholder="message" aria-label="message" aria-describedby="basic-addon1" v-model="msgInput" @keydown.enter="sendMsg">
                <button type="button" class="btn btn-outline-dark" @click="sendMsg">>></button>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Chating',
    mounted(){
        this.socket.on('roomIn', data => {this.userList = data.userList; this.roomInfo = data.roomInfo;});
        this.socket.on('chatingAwesome', data =>{this.chatList.push(data); this.scroll();});
    },
    data(){
        return{
            socket:this.$socket,
            roomInfo:[],
            userList:[],
            msgInput:'',
            chatList:[]
        }
    },
    methods:{
        sendMsg(){
        if(this.msgInput === "") return;
        this.socket.emit('chatingMsg', {msg:this.msgInput, roomId:this.roomInfo.roomId});
        this.msgInput = '';
        },
        scroll() {
        const msgBox = document.querySelector(".chating");
            let scrollInterval = setInterval(() => {  
            msgBox.scrollTop = msgBox.scrollHeight;
            clearInterval(scrollInterval);
        }, 10);
        }
    }
}
</script>

<style scoped>
    #chating{
        margin: 0 auto;
        width: 70%;
        height: 70%;
        background-color: white;
        border-radius: 10px;
        display: grid;
        grid-template-columns: 1fr 5fr;
        overflow: auto;
    }

    .title{
        background-color: #bdbdbd;
        text-align: center;
        box-shadow: 0px 3px 5px gray;
        margin-bottom: 5px;
        height: 22px;
    }

    .right{
        display: grid;
        grid-template-rows: 27px 1fr 27px 6fr;
        overflow: auto;
    }

    .roomInfo{
        display: grid;
        grid-template-rows: 1fr 1fr;
        text-align: center;
    }

    .userList{
        overflow-y: scroll;
        scrollbar-width: none;
        text-align: center;
    }   
    .userList::-webkit-scrollbar {
        display: none;
    }

    .user{
        width: 90%;
        margin: 0 auto;
        background-color: #e0e0e0;
        margin-bottom: 5px;
        box-shadow: 0px 3px 5px gray;
    }

    .user.my{
        background-color: #e2bc3d;
    }

    .left{
        width: 100%;
        height: 100%;
        overflow: auto;
    }

    .publicChat{
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-rows: 27px 10fr 1fr;
    }

    .chating{
        overflow-y: scroll;
        scrollbar-width: none;
    }
    .chating::-webkit-scrollbar {
        display: none;
    }

    .chat{
        background-color: #cccccc;
        margin-bottom: 8px;
        box-shadow: 0px 2px 3px gray;
    }

    .chat.my{
        background-color: #e2bc3d;
    }

    .send{
        display: grid;
        grid-template-columns: 9fr 1fr;
    }
</style>


